import { Injectable } from '@angular/core';
import { Course} from './course';
import { Observable, of } from 'rxjs';
import {HttpClient} from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class CourseService {

  constructor(private http: HttpClient) { }

  private coursesUrl = 'http://13.235.79.180/coursecategory/';
  private courseCategoriesUrl = 'http://13.235.79.180/coursecategories';
  private courseUrl = 'http://13.235.79.180/coursedetails/';

  public getCourses(name: string) {
    const url = `${this.coursesUrl}/${name}`;
    return this.http.get<Course[]>(url);
  }

  getCourseCategories(): Observable<Course[]> {
    return this.http.get<Course[]>(this.courseCategoriesUrl);
  }


  getCourse(id: number): Observable<Course> {
    const url = `${this.courseUrl}/${id}`;
    return this.http.get<Course>(url);
  }
}
