import {Component, Input, OnInit} from '@angular/core';
import { Course} from '../course';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import {CourseService} from '../course.service';

@Component({
  selector: 'app-course-detail',
  templateUrl: './course-detail.component.html',
  styleUrls: ['./course-detail.component.css']
})
export class CourseDetailComponent implements OnInit {

  @Input() course: Course;

  constructor(private route: ActivatedRoute,
              private courseService: CourseService,
              private location: Location) { }

  ngOnInit() {
    this.getCourse();
  }

  getCourse(): void {
    const id = +this.route.snapshot.paramMap.get('id');
    this.courseService.getCourse(id)
      .subscribe(course => this.course = course);
  }

  goBack(): void {
    this.location.back();
  }

  speech(): void {
    let speechText = '';
    for (let i = 0; i < this.course.fields.length; ) {
      speechText = speechText + this.course.fields[i].fieldName + '. ';
      speechText = speechText + this.course.fields[i].fieldValue + '. ';
      i = i + 1;
    }
    for (let j = 0; j < speechText.length; ) {
      const text1 = new SpeechSynthesisUtterance(speechText.substring(j, j + 2000));
      speechSynthesis.speak(text1);
      j = j + 2000;
    }
  }cancel(): void {
    speechSynthesis.cancel();
  }pause(): void {
    speechSynthesis.pause();
  }resume(): void {
    speechSynthesis.resume();
  }

}
