import {ChangeDetectorRef, Component, OnDestroy, OnInit} from '@angular/core';
import { Course } from '../course';
import { CourseService} from '../course.service';
import {ActivatedRoute} from '@angular/router';
import {Location} from '@angular/common';
import {MediaMatcher} from '@angular/cdk/layout';

@Component({
  selector: 'app-courses',
  templateUrl: './courses.component.html',
  styleUrls: ['./courses.component.css']
})
export class CoursesComponent implements OnInit, OnDestroy {

  courses: Course[];
  selectedCourse: Course;
  coursecategory: string;
  private sub: any;
  mobileQuery: MediaQueryList;
  private mobileQueryListener: () => void;

  constructor(private courseService: CourseService, private route: ActivatedRoute, private location: Location,
              media: MediaMatcher, changeDetectorRef: ChangeDetectorRef) {
    this.mobileQuery = media.matchMedia('(max-width: 600px)');
    this.mobileQueryListener = () => changeDetectorRef.detectChanges();
    this.mobileQuery.addListener(this.mobileQueryListener);
  }

  ngOnInit() {
    this.getCourses();
    this.sub = this.route.params.subscribe(params => {
      console.log(this.route.params);
    });
  }

  getCourses(): void {
    this.route.params.subscribe(routeParams => {
      this.coursecategory = routeParams.name;
      this.courseService.getCourses(this.coursecategory).subscribe(courses => {
        this.courses = courses;
      });
    });
  }

  onSelect(course: Course): void {
    this.selectedCourse = course;
  }

  goBack(): void {
    this.location.back();
  }

  ngOnDestroy(): void {
    this.sub.unsubscribe();
  }

}
